.project{
    background-color: #fff;
}
img.project__img{
    border-radius: 10px;
    width: 100%;
    object-fit: cover;
    transition: .4s;
    position: relative;
}
img.project__img::after{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 1;
    opacity: .1;
}
.mask__effect{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #a48829;
    border-radius: 10px;
    transition: .4s;
    opacity: .3;
    display: none;
}
.project__box:hover .mask__effect{
    display: inline;
    transform: scale(1.1);
}
.project__box:hover img.project__img{
    transform: scale(1.1);
}
.project__box:hover h5.project__text{
    color: #000;
    font-weight: 700;
}
.project__box:hover h4.project__text{
    color: #000;
    font-weight: 600;
}
.project__box:hover a.project__btn{
    color: #000;
    font-weight: 700;
    border: 2px solid #000;
    margin-bottom: 40px;
}
.project__meta.absolute{
    top: 0%;
    padding: 0px 20px;
    left: 0%;
    display: flex;
    height: 100%;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
    overflow: hidden;
    width: 100%;
}
h5.project__text{
    font-weight: 400;
    font-size: 20px;
    padding-bottom: 20px;
}
h4.project__text{
    font-size: 22px;
    font-family: Poppins,sans-serif;
    font-weight: 700;
    padding-bottom: 20px;
}
a.project__btn{
    padding: 12px 35px;
    border: 2px solid #ffffff87;
    border-radius: 5px;
    margin-bottom: -50px;
    transition: .4s ease;
}
a.project__btn:hover{
    background-color: #fff;
    border: 2px solid #000;
    margin-bottom: 40px;
}
button.btn{
    padding: 15px 20px;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    z-index: 100;
    border: 2px solid #a48829;
    background-color: #000;
    color: #a48829!important;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .4s ease;
}
button.view__more:hover{
    background-color: #a48829;
    border: 2px solid #000;
    color: #000!important;
}
button.view__more:hover a.viewmore{
    color: #000!important;
}
a.viewmore{
    color: #a48829;
}
/* RESPONSIVE */
@media only screen and (max-width: 1024){
    h5.project__text{
        font-weight: 400;
        font-size: 20px;
        padding-bottom: 20px;
    }
    h4.project__text{
        font-size: 20px;
        text-align: center;
    }
}