.mission{
    background-color: #fff;
}
.col__3{
    align-items: center;
}

  .flip-box {
    background-color: transparent;
    border-radius: 10px;
    width: 400px;
    height: 400px;
    border: 1px solid #fff;
    perspective: 1000px;
  }
  .flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  .flip-box:hover .flip-box-inner {
    transform: rotateY(180deg);
  }
  .flip-box-front, .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  .flip-box-front {
    background-color: #000;
    color: #fff;
  }
  .flip-box-back {
    background-color: #a48829;
    color: white;
    transform: rotateY(180deg);
  }

h1.pq{
    color: #fff;
    padding: 10px 10px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-size: 50px;
}
h2.rs{
    color: #fff;
    padding: 10px 10px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-size: 30px;
}

h1.mission__heading{
    margin-bottom: 10px;
    font-weight: 700;
    text-align: center;
    font-family: Montserrat,sans-serif;
    font-size: 60px;
}
img.mission__img{
    border-radius: 10px;
    width: 100%;
    object-fit: cover;
    transition: .4s;
    position: relative;
}
img.mission__img::after{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 1;
    opacity: .1;
}
p.mission__text.p__color{
    margin-top: 10px;
    color: #fff;
    z-index: 100;
}
@media only screen and (max-width: 1024px){
    .p__color{
        font-size: 15px!important;
    }
    .flip-box {
        background-color: transparent;
        width: 350px;
        height: 350px;
        border: 1px solid #fff;
        perspective: 1000px;
        padding: 32px;
      }
    h1.pq{
        font-size: 40px;
    }
    h2.rs{
        font-size: 20px;
    }
    h1.mission__heading{
      text-align: center;
    }
}
