h1.heading{
    font-size: 60px;
    text-align: center;
    font-family: Poppins,sans-serif;
    font-weight: 700;
    margin-bottom: 10px;
    z-index: 100;
}
p.heading.p__color{
    margin-bottom: 0;
    text-align: center;
    line-height: 1.2;
}
.service__box.pointer{
    border-radius: 10px;
    width: 100%;
    background-color: #000;
    padding: 55px;
    transition: .4s ease;
    z-index: 100;
}
.service__box:hover{
    transform: translate(5px);
    background-image:linear-gradient(90deg, #a48829, #f8f0c9);
}
.service__box:hover .icon{
    color: #000;
}
.service__box:hover .p.service__text.p__color{
    color: #000!important;
}
.service__box:hover .h1.service__text{
    color: #000!important;
}
.icon{
    font-size: 54px;
    font-weight: 400;
    margin-bottom: 23px;
    display: inline-flex;
    color: #a48829;
}
p.service__text.p__color{
    margin-bottom: 0;
    z-index: 100;
}
h1.service__tex{
    font-size: 19px;
    font-family: Poppins,sans-serif;
    padding: 10px 0px;
    z-index: 100;
}
/* RESPONSIVE */
@media only screen and (max-width: 1024){
    .service__box.pointer{
        padding: 32px;
    }
}