.about{
    background: #fff;
}
img.about__img{
    border-radius: 10px;
    width: 50%;
    border: 3px solid #000;
    z-index: 100;
}
.col__2{
    align-items: center;
    width: 50%;
}
p.about__text.p__color{
    margin-top: 10px;
    color: #fff;
    z-index: 100;
}
h1.about__heading{
    margin-bottom: 10px;
    font-weight: 700;
    text-align: center;
    font-family: Montserrat,sans-serif;
    font-size: 60px;
}
a.bottom__to__top{
    width: 40px;
    height: 40px;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #a48829;
    color: #000;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    transition: .4s all ease;
}
a.bottom__to__top.active{
    display: flex;
}
/* RESPONSIVE */
@media only screen and (max-width: 1024px){
    .p__color{
        font-size: 15px!important;
    }
}
@media only screen and (max-width: 920px){
    img.about__img{
        border-radius: 10px;
        width: 100%;
        border: 3px solid #000;
        z-index: 100;
    }
    .col__2{
        align-items: center;
        width: 100%;
    }
}