.home__bg{
background-image: url("C:/Users/Lenovo/Desktop/Website/portfolio/src/img/bg.jpg");
background-position: 50%;
background-repeat: no-repeat;
background-size: cover;
}
.home__meta{
    justify-content: flex-end;
}
a.abc{
    display: flex;
    align-items: center;
    padding: 0px 20px;
}
.header.d__flex.align__items__center{
    padding-left: 30px;
    padding-bottom: 30px;
    padding-right: 30px;
}
li.nav__items{
    font-size: 24px;
    padding-bottom: 0px;
    z-index: 100;
}
.home__content{
    padding: 190px 0px;
}
h1.home__text{
    font-size: 18px;
    margin-top: 150px;
    font-weight: 400;
    letter-spacing: 3px;
}
h2.home__text{
    font-size: 54px;
    line-height: 54px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
h3.home__text{
    font-size: 50px;
    line-height: 40px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
h4.home__text{
    font-size: 50px;
    line-height: 40px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
.header{
    transition: .4s all ease;
}
.header.d__flex.align__items__center.active{
    position: fixed;
    align-items: center;
    z-index: 1000;
    width: 100%;
    height: auto;
    padding: 0px 30px;
    background-color: #fff;
}
.navigation{
    width: 100%;
}
.logo{
    padding: 0px 30px;
}
.toggle__menu{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    float: right;
    z-index: 1000;
}
svg.bi.bi-list.pointer{
    width: 30px;
    margin: 0px 16px;
    height: 30px;
    color: #a48829;
}
ul.navbar.d__flex{
    justify-content: center;
    margin-top: 8px;
}
a.xyz{
    color: #fff;
}
ul.sidebar.d__flex{
    flex-wrap: wrap;
    height: 100%;
    position: fixed;
    right: 0%;
    background: #010101;
    width: 150px;
    justify-content: center;
    top: 0%;
    flex-direction: column;
}
li.sideNavbar{
    padding: 25px 30px;
}


/* RESPONSIVE */
@media only screen and (max_width: 2500px){
    .toggle__menu{
        display: none;
    }
    ul.sidebar.d__flex{
        display: none;
    }
    svg.bi.bi-list.pointer{
        display: none;
    }
}

@media only screen and (max-width: 1150px){
    .header{
        justify-content: space-between;
        
    }
    a.abc{
        display: none;
    }
    ul.sidebar.d__flex{
        padding-left: 30px;
    }
    .header.d__flex.align__items__center {
        justify-content: space-between;
        padding-right: 0px;
    }
    .header.d__flex.align__items__center.active {
        justify-content: space-between;
        padding-right: 0px;
    }
    h2.white.pz__10{
        font-size: 50px;
    }
    h3.gold.pz__10{
        font-size: 30px;
    }
    svg.bi.bi-list.pointer{
        width: 30px;
        height: 30px;
        margin: 0px 0px;
    }
}
@media only screen and (max-width: 425px){
    h2.home__text.pz__10{
        font-size: 32px;
    }
    .header.d__flex.align__items__center {
        justify-content: space-between;
        padding-right: 0px;
    }
    .header.d__flex.align__items__center.active {
        justify-content: space-between;
        padding-right: 0px;
    }
    h3.gold.pz__10{
        font-size: 30px;
    }
    h1.home__text{
        font-size: 14px;
    }
    svg.bi.bi-list.pointer{
        width: 30px;
        height: 30px;
        margin: 0px 0px;
    }
}
@media only screen and (max-width: 850px){
   
    .home__content{
        padding: 350px 0px;
    }
}

@media only screen and (max-width: 415px){
   
    .home__content{
        padding: 220px 0px;
    }
}

@media only screen and (max-width: 376px){
   
    .home__content{
        padding: 100px 0px;
    }
}
