.contact{
    background-color: #fff;
}
h1.hire__text{
    font-size: 60px;
    z-index: 100;
    text-align: center;
}
.contact__box{
    width: 75%;
    align-self: end;
}
.col__2__custom{
    width: 50%;
    display: flex;
    padding: 20px;
    margin: 20px 0px 0px;
    justify-content: center;
    flex-direction: column;
}
.col__2__cust{
    width: 50%;
    display: flex;
    padding: 20px;
    margin: 20px 0px 0px;
    justify-content: center;
    flex-direction: column;
}
.row2{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    overflow: hidden;
    align-items: flex-start;
    justify-content: center;
}
p.hire__text{
    font-size: 18px;
    line-height: 30px;
    color: rgba(29, 29, 36, .75);
    font-weight: 400;
    z-index: 100;
    text-align: center;
}
h2.ba{
    font-size: 12px;
    color: #a48829;
}
.map-canvas {
    height: 500px;
    width: 100%;
    margin-top: -17px;
}
input.contact{
    display: block;
    width: 100%;
    padding: 0 20px;
    border-radius: 5px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    height: 50px;
    line-height: 46px;
    margin: 20px 0px;
    color: #000;
    font-size: 15px;
    letter-spacing: .1px;
    background: none;
    border: 2px solid #000;
}
textarea#message{
    height: 120px;
    background: none;
    padding: 0 20px;
    width: 100%;
    line-height: 46px;
    color: #000;
    font-size: 15px;
    letter-spacing: .1px;
    border: 2px solid #000;
}
button.btn.contact.pointer{
    margin: 20px 0px;
    padding: 0px 20px;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    z-index: 100;
    opacity: 100%;
    border: 2px solid #a48829;
    background-color: #000;
    color: #a48829!important;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .4s ease;
}
button.btn.contact.pointer:hover{
    background-color: #a48829;
    border: 2px solid #000;
    opacity: 100%;
    color: #000!important;
    z-index: 100;
}
.contact__img{
    border-radius: 10px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 100;
}
@media only screen and (max-width: 920px) {
    .col__2__custom {
        width: inherit;
        max-width: 100%;
        flex-direction: row;
    }
    .col__2__cust {
        width: inherit;
        max-width: 100%;
    }
}